<template>
  <div v-for="items in menu" :key="items" class="w-full">
    <div v-for="(value,index) in items" :key="value">
      <div class="flex flex-row align-center justify-between w-full m-3">
        <div class="flex flex-row align-center w-10/12 my-2">
          <span class="text-black font-bold text-sm">{{index+=1}}</span>
          <img @error="notShow" :src="`${value.player.image}`" alt="" class="mx-2 w-10 h-10 rounded-full ">
          <div class="flex flex-col space-y-1">
            <div><p class="text-black font-thin">{{value.player.name_fa}}</p></div>
            <div class="flex flex-row">
              <img @error="notShow" :src="`${value.team.logo}`" alt="" class="w-5 h-5 rounded-full">
              <p class="text-black font-thin">{{value.team.name_fa}}</p>
            </div>
          </div>
        </div>
        <div class="w-2/12">
          <p class="text-black font-thin text-start">{{value.assist}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {ref} from "vue";

export default {
  name: "NumberAssist",
  props:{
    menu:{
      type:Array,
      required:true,
      default:null
    }
  },
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_player_default.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
      notFound,notShow
    }
  }
}
</script>

