<template>
<section class="center-pass-goals">
  <div class="container mx-auto p-1 sm:mx-1 max-w-screen-md">
    <div class="w-full h-auto">
      <div class="flex flex-col space-y-1">
        <p class="text-slate-600 font-bold text-end justify-end w-full">پاس گل</p>
        <Suspense>
          <template #default>
            <NumberAssist :menu="pass"/>
          </template>
          <template #fallback>
            <LoaderComponentTwo/>
          </template>
        </Suspense>

      </div>
    </div>
  </div>
</section>
</template>

<script>

import{reactive,onMounted} from "vue";
import EndPoints from "@/service/EndPoints";
import NumberAssist from "@/components/main/competitions/NumberAssist";
import LoaderComponentTwo from "@/components/composable/LoaderComponentTwo";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "BesePassGoals",
  components: {LoaderComponentTwo, NumberAssist},
  setup(){

    const pass=reactive([])
    const result=onMounted( async ()=>{
      await EndPoints.getPersianGulfs()
          .then((response)=>{
            if(response.data.success){
              pass.push(response.data.data.assist)
            }
          })
          .catch((error)=>{
           handleError(error)
          })
    })
    return{
      pass,
     result,

    }
  }
}
</script>

